import {Injectable} from '@angular/core';
import {Clone} from './clone';
import {HttpClient, HttpErrorResponse} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/do';
import {ErrorObservable} from 'rxjs/observable/ErrorObservable';

@Injectable()
export class CloneService {
  private _clone: Clone;
  private baseUrl = 'localhost:8080/cyberpunk/clones';
  private mensagemErro = null;

  constructor(private _http: HttpClient) {
  }

  obterClones(): Observable<Clone[]> {
    return this._http
      .get<Clone[]>('http://' + this.baseUrl)
      .catch(this.handleError);
  }

  obterClone(id: number): Clone {
    return this.getClone();
  }

  pesquisarClone(nome: string): Observable<Clone> {
    return this._http
      .get('http://' + this.baseUrl + '/' + nome)
      .catch(this.handleError);
  }

  salvarClone(clone: Clone): Observable<Clone> {
    return this._http
      .post('http://' + this.baseUrl, clone)
      .catch(this.handleError);
  }

  atualizarClone(nome: string, clone: Clone): Observable<Clone> {
    return this._http
      .put('http://' + this.baseUrl + '/' + nome, clone)
      .catch(this.handleError);
  }

  deletarClone(nome: string): Observable<Clone> {
    return this._http
      .delete('http://' + this.baseUrl + '/' + nome)
      .catch(this.handleError);
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      console.error('Ocorreu um error:', error.error.message);
    } else {
      this.mensagemErro = error.error;
      // console.error(
      //   `Status ${error.status}, ` +
      //   `mensagem: ${JSON.stringify(error.error.errors)} \n` +
      //   `ERROR>>> ${error}`);
    }
    return new ErrorObservable(error.error.errors);
  }

  setClone(clone: Clone) {
    this._clone = clone;
  }

  getClone(): Clone {
    return this._clone;
  }

  getMensage() {
    return this.mensagemErro;
  }
}



