import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {Clone} from '../clone';
import {CloneService} from '../clone.service';

@Component({
  selector: 'app-listar-clones',
  templateUrl: './listar-clones.component.html',
  styleUrls: ['./listar-clones.component.css']
})
export class ListarClonesComponent implements OnInit {
  clones: Clone[] = [];
  exibir: Boolean = false;

  constructor(private router: Router,
              private service: CloneService) {
    this.clones = [];
  }

  ngOnInit() {
    this.service.obterClones().subscribe(data => {
      this.clones = data;
    }, err => {
      console.error(err);
    });
  }

  editarClone(clone: Clone) {
    this.service.setClone(clone);
    this.router.navigate(['/clones', clone.id]);
  }

  novo() {
    this.router.navigate(['/clones/novo']);
  }

  listarAdicionais(clone: Clone) {
    this.service.setClone(clone);
    this.exibir = true;
    this.router.navigate(['/clones/adicionais', clone.nome]);
  }

}



