import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';


import { AppComponent } from './app.component';
import { ListarClonesComponent } from './listar-clones/listar-clones.component';
import { EditarCloneComponent } from './editar-clone/editar-clone.component';
import { AdicionaisCloneComponent } from './adicionais-clone/adicionais-clone.component';
import {CloneService} from './clone.service';
import {CampoControlErroComponent} from './campo-control-erro/campo-control-erro.component';

const appRoutes: Routes = [
  { path: 'clones', component: ListarClonesComponent },
  { path: 'clones/:id', component: EditarCloneComponent },
  { path: 'clones/adicionais/:id', component: AdicionaisCloneComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    ListarClonesComponent,
    EditarCloneComponent,
    AdicionaisCloneComponent,
    CampoControlErroComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [CloneService],
  bootstrap: [AppComponent]
})
export class AppModule { }



