import {Component} from '@angular/core';
import {Router} from '@angular/router';
import {CloneService} from './clone.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  search = '';

  constructor(private service: CloneService, private router: Router) {
  }

  pesquisarClone(campo) {
    console.log(campo.value);
    const s = campo.value.search.toUpperCase();
    this.service.pesquisarClone(s).subscribe(data => {
      this.service.setClone(data);
      this.router.navigate(['/clones/adicionais', s]);
      console.log(JSON.stringify(data));
    }, err => {
      console.error(err);
    });
  }
}



