import {Component, OnInit, Input} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {CloneService} from '../clone.service';
import {Clone} from '../clone';
import {forEach} from '@angular/router/src/utils/collection';
import {Adicionais} from '../adicionais';

@Component({
  selector: 'app-editar-clone',
  templateUrl: './editar-clone.component.html',
  styleUrls: ['./editar-clone.component.css']
})
export class EditarCloneComponent implements OnInit {

  @Input()
  _clone: Clone = new Clone();
  adicionais = new Adicionais();
  oldClone: string;
  novo: Boolean = false;
  mostrarErro: Boolean = false;
  msgErro = {};

  constructor(private service: CloneService,
              private route: ActivatedRoute,
              private router: Router) {
  }

  ngOnInit() {
    let id = +this.route.snapshot.params['id'];
    if (id) {
      this._clone = this.service.obterClone(id);
      this.oldClone = this._clone.nome;
      this.adicionais = this._clone.adicionais;
    } else {
      this.novo = true;
    }
  }

  salvar(form) {
    const clone = form.value;
    clone.adicionais = this.adicionais;
    if (this.novo) {
      this.service.salvarClone(clone).subscribe(data => {
        this.router.navigate(['/clones']);
      }, err => {
        this.mostrarErro = true;
        this.msgErro = this.setErros(err);
        // console.log(err);
      });
    } else {
      this.service.atualizarClone(this.oldClone, clone).subscribe(data => {
        // console.log(JSON.stringify(data));
        this.router.navigate(['/clones']);
      }, err => {
        this.mostrarErro = true;
        this.msgErro = this.setErros(err);

      });
    }
  }

  deletar(nome: string) {
    this.service.deletarClone(nome).subscribe(data => {
      this.router.navigate(['/clones']);
    });
  }

  verificaValidTouched(campo) {
    return !campo.valid && campo.touched;
  }

  aplicaCssErro(campo) {
    return {
      'has-error': this.verificaValidTouched(campo),
      'has-feedback': this.verificaValidTouched(campo)
    };
  }

  mensagemErro(campo): string {
    const m = this.service.getMensage();
    return this.tratarMensagem(campo, m);
  }

  private tratarMensagem(campo, err): string {
    if (err != null) {
      err.forEach(e => {
        if (e.field.equals(campo)) {
          return e.defaultMessage.toString();
        } else {
          return 'Este campo não é valido';
        }
      });
    } else {
      return 'Este campo é obrigatorio';
    }
  }

  private setErros(err): string[] {
    const array = new Array();
    if (err != null) {
      for (var e of err) {
        if (e.tipo === false) {
          array.push(`O valor '${e.rejectedValue}' não é válido!  ${e.defaultMessage}.`);
        } else {
          array.push(e.defaultMessage + '.');
        }
      }
    } else {
      array.push(this.service.getMensage());
    }
    return array;
  }
}



